export const Detail =(props) =>{
    let [likecnt, likecntchk] = useState(0);
    let {pid, ln} = useParams();
    let nav = useNavigate();
    let pidImg = pid > ln ? 0 : pid;
    
    return(
        <Container>
            <div className='imgBox'>
                <div className='imgCon' alt={""+pid} style={{backgroundImage:`url(/shop/img/jp${pidImg}.png)`}} />
            <h4>{pid}</h4>
            <p>{pid} </p>
            <p className='lkcnt' onClick={()=> likecntchk(++likecnt)}> {likecnt}</p>
            </div>
            <Button primary onClick={()=>nav(-1)}> </Button><br />
            <CustomBtn bg={'#000'} cl={'#fff'}> </CustomBtn>
            <CustomBtn bg={'#f00'} cl={'#fff'}> </CustomBtn>
        </Container>
    );
}
