//useEffect() mount,update  
/*
    useEffect() html  .
          ,     .
    html rendering          Side Effect  .
*/

function ComLife(){
    let [age, ageChk] = useState(20);
    let [at, alertchk] = useState(true);

    useEffect(() =>{
        let timer = setTimeout(() =>{alertchk(false)}, 5000);
        //return  ,,   

        return() => {
            alert(', ');
            clearTimeout(timer);
        }
    }, []);

    /*
    1. (,  )     
    useEffect(() => {});

    2. mount() 1    
    useEffect(() =>{},[]);

    3. , ,      
    useEffect(() => {
        return() => {
            ...;
        }
    }, []); => 
    */

    return(
        <div>
              {age} .<br />
            {
                at === true ? <button onClick={()=> ageChk(++age)}></button> : null
            }
        </div>
    );
    
}

export default ComLife;



